"use client";
import Header from "@/components/Header";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
	Breadcrumb,
	BreadcrumbItem,
	BreadcrumbLink,
	BreadcrumbList,
	BreadcrumbPage,
	BreadcrumbSeparator,
} from "@/components/ui/breadcrumb";
import useCart from "@/hooks/useCart";
import { useState } from "react";
import {
	Form,
	FormControl,
	FormField,
	FormItem,
	FormLabel,
	FormMessage,
} from "@/components/ui/form";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import {
	Card,
	CardContent,
	CardDescription,
	CardHeader,
	CardTitle,
} from "@/components/ui/card";
import {
	Select,
	SelectContent,
	SelectItem,
	SelectTrigger,
	SelectValue,
} from "@/components/ui/select";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Label } from "@/components/ui/label";
import { useRouter } from "next/navigation";
import { toast } from "sonner";
import { Loader2, CreditCard, Truck, Home } from "lucide-react";

// Schema de validação para o formulário
const formSchema = z.object({
	deliveryMethod: z.enum(["standard", "express", "pickup"], {
		required_error: "Selecione um método de entrega",
	}),
	shippingAddress: z.object({
		street: z.string().min(1, "Rua é obrigatória"),
		number: z.string().min(1, "Número é obrigatório"),
		complement: z.string().optional(),
		neighborhood: z.string().min(1, "Bairro é obrigatório"),
		city: z.string().min(1, "Cidade é obrigatória"),
		state: z.string().length(2, "UF deve ter 2 caracteres"),
		zipCode: z.string().min(8, "CEP inválido").max(9),
	}),
	paymentMethod: z.enum(["credit", "debit", "pix"], {
		required_error: "Selecione um método de pagamento",
	}),
	cardInfo: z
		.object({
			number: z.string().min(16, "Número do cartão inválido").optional(),
			name: z.string().min(3, "Nome inválido").optional(),
			expiry: z.string().min(5, "Validade inválida").optional(),
			cvv: z.string().min(3, "CVV inválido").max(4).optional(),
		})
		.optional(),
	billingSameAsShipping: z.boolean().default(true),
});

export default function Checkout() {
	const { cart, itemCount, cartTotal, clearCart } = useCart();
	const router = useRouter();
	const [isSubmitting, setIsSubmitting] = useState(false);

	// Formulário
	const form = useForm<z.infer<typeof formSchema>>({
		resolver: zodResolver(formSchema),
		defaultValues: {
			deliveryMethod: "standard",
			shippingAddress: {
				street: "",
				number: "",
				complement: "",
				neighborhood: "",
				city: "",
				state: "",
				zipCode: "",
			},
			paymentMethod: "credit",
			cardInfo: {
				number: "",
				name: "",
				expiry: "",
				cvv: "",
			},
			billingSameAsShipping: true,
		},
	});

	// Cálculos
	const subtotal = cart.reduce(
		(sum, item) => sum + item.price * item.quantity,
		0
	);

	const deliveryMethod = form.watch("deliveryMethod");
	const shippingFee =
		deliveryMethod === "express"
			? 25.9
			: deliveryMethod === "pickup"
			? 0
			: subtotal > 200
			? 0
			: 15.9;

	const discount = 0; // Cupom seria implementado aqui
	const total = subtotal + shippingFee - discount;

	const handleSubmit = async (values: z.infer<typeof formSchema>) => {
		setIsSubmitting(true);

		try {
			// Simulando processamento do pedido
			await new Promise((resolve) => setTimeout(resolve, 2000));

			// Limpar carrinho após sucesso
			clearCart();

			toast.success("Pedido realizado com sucesso!");
			router.push("/order-confirmation");
		} catch (error) {
			toast.error("Erro ao processar pedido");
			console.error(error);
		} finally {
			setIsSubmitting(false);
		}
	};

	// Se o carrinho estiver vazio, redirecionar
	if (cart.length === 0) {
		return (
			<main className="min-h-screen flex flex-col">
				<Header />
				<div className="container mx-auto px-4 py-6 flex-1 flex items-center justify-center">
					<div className="text-center">
						<h1 className="text-2xl font-bold mb-4">Seu carrinho está vazio</h1>
						<Button asChild>
							<a href="/products">Continuar Comprando</a>
						</Button>
					</div>
				</div>
			</main>
		);
	}

	return (
		<main className="min-h-screen flex flex-col">
			<Header />

			{/* Breadcrumb */}
			<div className="container mx-auto px-4 py-4">
				<Breadcrumb>
					<BreadcrumbList>
						<BreadcrumbItem>
							<BreadcrumbLink href="/">Home</BreadcrumbLink>
						</BreadcrumbItem>
						<BreadcrumbSeparator />
						<BreadcrumbItem>
							<BreadcrumbLink href="/cart">Carrinho</BreadcrumbLink>
						</BreadcrumbItem>
						<BreadcrumbSeparator />
						<BreadcrumbItem>
							<BreadcrumbPage>Checkout</BreadcrumbPage>
						</BreadcrumbItem>
					</BreadcrumbList>
				</Breadcrumb>
			</div>

			{/* Conteúdo principal */}
			<div className="container mx-auto px-4 py-6 flex-1 flex flex-col lg:flex-row gap-8">
				{/* Seção de formulários - 60% da largura */}
				<section className="lg:w-3/5">
					<Form {...form}>
						<form
							onSubmit={form.handleSubmit(handleSubmit)}
							className="space-y-8"
						>
							{/* Método de Entrega */}
							<Card>
								<CardHeader>
									<CardTitle className="flex items-center gap-2">
										<Truck className="w-5 h-5" /> Método de Entrega
									</CardTitle>
									<CardDescription>
										Selecione como deseja receber seu pedido
									</CardDescription>
								</CardHeader>
								<CardContent>
									<FormField
										control={form.control}
										name="deliveryMethod"
										render={({ field }) => (
											<FormItem>
												<RadioGroup
													onValueChange={field.onChange}
													defaultValue={field.value}
													className="grid grid-cols-1 md:grid-cols-3 gap-4"
												>
													<div>
														<RadioGroupItem
															value="standard"
															id="standard"
															className="peer sr-only"
														/>
														<Label
															htmlFor="standard"
															className="flex flex-col items-center justify-between rounded-md border-2 border-muted bg-popover p-4 hover:bg-accent hover:text-accent-foreground peer-data-[state=checked]:border-primary [&:has([data-state=checked])]:border-primary"
														>
															<div className="mb-2">
																<Truck className="w-6 h-6" />
															</div>
															Entrega Padrão
															<p className="mt-1 text-sm text-gray-500">
																{shippingFee === 0
																	? "Grátis"
																	: `R$ ${shippingFee.toFixed(2)}`}
															</p>
															<p className="text-xs mt-1">3-5 dias úteis</p>
														</Label>
													</div>
													<div>
														<RadioGroupItem
															value="express"
															id="express"
															className="peer sr-only"
														/>
														<Label
															htmlFor="express"
															className="flex flex-col items-center justify-between rounded-md border-2 border-muted bg-popover p-4 hover:bg-accent hover:text-accent-foreground peer-data-[state=checked]:border-primary [&:has([data-state=checked])]:border-primary"
														>
															<div className="mb-2">
																<Truck className="w-6 h-6 text-blue-500" />
															</div>
															Entrega Expressa
															<p className="mt-1 text-sm text-gray-500">
																R$ 25,90
															</p>
															<p className="text-xs mt-1">1-2 dias úteis</p>
														</Label>
													</div>
													<div>
														<RadioGroupItem
															value="pickup"
															id="pickup"
															className="peer sr-only"
														/>
														<Label
															htmlFor="pickup"
															className="flex flex-col items-center justify-between rounded-md border-2 border-muted bg-popover p-4 hover:bg-accent hover:text-accent-foreground peer-data-[state=checked]:border-primary [&:has([data-state=checked])]:border-primary"
														>
															<div className="mb-2">
																<Home className="w-6 h-6 text-green-500" />
															</div>
															Retirar na Loja
															<p className="mt-1 text-sm text-green-600">
																Grátis
															</p>
															<p className="text-xs mt-1">Disponível em 1h</p>
														</Label>
													</div>
												</RadioGroup>
												<FormMessage />
											</FormItem>
										)}
									/>
								</CardContent>
							</Card>

							{/* Endereço de Entrega */}
							<Card>
								<CardHeader>
									<CardTitle className="flex items-center gap-2">
										<MapPin className="w-5 h-5" /> Endereço de Entrega
									</CardTitle>
									<CardDescription>
										Informe onde deseja receber seu pedido
									</CardDescription>
								</CardHeader>
								<CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
									<FormField
										control={form.control}
										name="shippingAddress.street"
										render={({ field }) => (
											<FormItem className="md:col-span-2">
												<FormLabel>Rua</FormLabel>
												<FormControl>
													<Input placeholder="Nome da rua" {...field} />
												</FormControl>
												<FormMessage />
											</FormItem>
										)}
									/>

									<FormField
										control={form.control}
										name="shippingAddress.number"
										render={({ field }) => (
											<FormItem>
												<FormLabel>Número</FormLabel>
												<FormControl>
													<Input placeholder="Número" {...field} />
												</FormControl>
												<FormMessage />
											</FormItem>
										)}
									/>

									<FormField
										control={form.control}
										name="shippingAddress.complement"
										render={({ field }) => (
											<FormItem>
												<FormLabel>Complemento</FormLabel>
												<FormControl>
													<Input placeholder="Apto, bloco, etc." {...field} />
												</FormControl>
												<FormMessage />
											</FormItem>
										)}
									/>

									<FormField
										control={form.control}
										name="shippingAddress.neighborhood"
										render={({ field }) => (
											<FormItem>
												<FormLabel>Bairro</FormLabel>
												<FormControl>
													<Input placeholder="Bairro" {...field} />
												</FormControl>
												<FormMessage />
											</FormItem>
										)}
									/>

									<FormField
										control={form.control}
										name="shippingAddress.city"
										render={({ field }) => (
											<FormItem>
												<FormLabel>Cidade</FormLabel>
												<FormControl>
													<Input placeholder="Cidade" {...field} />
												</FormControl>
												<FormMessage />
											</FormItem>
										)}
									/>

									<FormField
										control={form.control}
										name="shippingAddress.state"
										render={({ field }) => (
											<FormItem>
												<FormLabel>Estado (UF)</FormLabel>
												<FormControl>
													<Input placeholder="UF" maxLength={2} {...field} />
												</FormControl>
												<FormMessage />
											</FormItem>
										)}
									/>

									<FormField
										control={form.control}
										name="shippingAddress.zipCode"
										render={({ field }) => (
											<FormItem>
												<FormLabel>CEP</FormLabel>
												<FormControl>
													<Input placeholder="00000-000" {...field} />
												</FormControl>
												<FormMessage />
											</FormItem>
										)}
									/>
								</CardContent>
							</Card>

							{/* Método de Pagamento */}
							<Card>
								<CardHeader>
									<CardTitle className="flex items-center gap-2">
										<CreditCard className="w-5 h-5" /> Pagamento
									</CardTitle>
									<CardDescription>Selecione como deseja pagar</CardDescription>
								</CardHeader>
								<CardContent>
									<FormField
										control={form.control}
										name="paymentMethod"
										render={({ field }) => (
											<FormItem>
												<RadioGroup
													onValueChange={field.onChange}
													defaultValue={field.value}
													className="flex flex-col space-y-1"
												>
													<div className="flex items-center space-x-3 space-y-0">
														<RadioGroupItem value="credit" id="credit" />
														<Label htmlFor="credit">Cartão de Crédito</Label>
													</div>
													<div className="flex items-center space-x-3 space-y-0">
														<RadioGroupItem value="debit" id="debit" />
														<Label htmlFor="debit">Cartão de Débito</Label>
													</div>
													<div className="flex items-center space-x-3 space-y-0">
														<RadioGroupItem value="pix" id="pix" />
														<Label htmlFor="pix">PIX</Label>
													</div>
												</RadioGroup>
												<FormMessage />
											</FormItem>
										)}
									/>

									{/* Campos do cartão (apenas se crédito/débito selecionado) */}
									{["credit", "debit"].includes(
										form.watch("paymentMethod")
									) && (
										<div className="mt-6 grid grid-cols-1 md:grid-cols-2 gap-4">
											<FormField
												control={form.control}
												name="cardInfo.number"
												render={({ field }) => (
													<FormItem className="md:col-span-2">
														<FormLabel>Número do Cartão</FormLabel>
														<FormControl>
															<Input
																placeholder="0000 0000 0000 0000"
																{...field}
															/>
														</FormControl>
														<FormMessage />
													</FormItem>
												)}
											/>

											<FormField
												control={form.control}
												name="cardInfo.name"
												render={({ field }) => (
													<FormItem className="md:col-span-2">
														<FormLabel>Nome no Cartão</FormLabel>
														<FormControl>
															<Input placeholder="Nome completo" {...field} />
														</FormControl>
														<FormMessage />
													</FormItem>
												)}
											/>

											<FormField
												control={form.control}
												name="cardInfo.expiry"
												render={({ field }) => (
													<FormItem>
														<FormLabel>Validade</FormLabel>
														<FormControl>
															<Input placeholder="MM/AA" {...field} />
														</FormControl>
														<FormMessage />
													</FormItem>
												)}
											/>

											<FormField
												control={form.control}
												name="cardInfo.cvv"
												render={({ field }) => (
													<FormItem>
														<FormLabel>CVV</FormLabel>
														<FormControl>
															<Input
																placeholder="000"
																type="password"
																{...field}
															/>
														</FormControl>
														<FormMessage />
													</FormItem>
												)}
											/>
										</div>
									)}
								</CardContent>
							</Card>

							{/* Botão de finalizar */}
							<div className="flex justify-end">
								<Button
									type="submit"
									className="w-full md:w-auto bg-green-600 hover:bg-green-700 h-12 text-lg px-8"
									disabled={isSubmitting}
								>
									{isSubmitting ? (
										<>
											<Loader2 className="mr-2 h-4 w-4 animate-spin" />
											Processando...
										</>
									) : (
										"Finalizar Compra"
									)}
								</Button>
							</div>
						</form>
					</Form>
				</section>

				{/* Seção de resumo do pedido - 40% da largura */}
				<section className="lg:w-2/5">
					<Card className="sticky top-6">
						<CardHeader>
							<CardTitle>Resumo do Pedido</CardTitle>
						</CardHeader>
						<CardContent>
							<div className="space-y-4">
								{/* Lista de produtos */}
								<div className="border-b pb-4">
									<h3 className="font-semibold mb-2">Produtos</h3>
									<div className="space-y-3 max-h-60 overflow-y-auto">
										{cart.map((item) => (
											<div key={item.id} className="flex justify-between">
												<div className="flex items-center">
													<div className="bg-gray-100 border rounded-md w-12 h-12 flex items-center justify-center mr-3">
														{item.imageUrl ? (
															<img
																src={item.imageUrl}
																alt={item.name}
																className="object-contain w-10 h-10"
															/>
														) : (
															<div className="bg-gray-200 border-2 border-dashed rounded-xl w-10 h-10" />
														)}
													</div>
													<div>
														<p className="font-medium">{item.name}</p>
														<p className="text-sm text-gray-500">
															{item.quantity} × R$ {item.price.toFixed(2)}
														</p>
													</div>
												</div>
												<span className="font-medium">
													R$ {(item.price * item.quantity).toFixed(2)}
												</span>
											</div>
										))}
									</div>
								</div>

								{/* Resumo financeiro */}
								<div className="space-y-2">
									<div className="flex justify-between">
										<span>Subtotal</span>
										<span>R$ {subtotal.toFixed(2)}</span>
									</div>

									<div className="flex justify-between">
										<span>Frete</span>
										<span>
											{shippingFee > 0
												? `R$ ${shippingFee.toFixed(2)}`
												: "Grátis"}
										</span>
									</div>

									{discount > 0 && (
										<div className="flex justify-between text-green-600">
											<span>Desconto</span>
											<span>- R$ {discount.toFixed(2)}</span>
										</div>
									)}

									<div className="h-px bg-gray-200 my-2"></div>

									<div className="flex justify-between font-bold text-lg">
										<span>Total</span>
										<span>R$ {total.toFixed(2)}</span>
									</div>
								</div>

								{subtotal > 0 && subtotal < 200 && shippingFee > 0 && (
									<div className="mt-4 text-center text-sm text-green-600 bg-green-50 p-2 rounded">
										Adicione mais R$ {(200 - subtotal).toFixed(2)} para frete
										grátis!
									</div>
								)}
							</div>
						</CardContent>
					</Card>
				</section>
			</div>
		</main>
	);
}
